"use client";

import React from "react";

type BlogPostClientProps = {
  post: {
    title: string;
    content: string;
  };
  className?: string; // optional Tailwind overrides
};

export default function BlogPostClient({ post, className = "" }: BlogPostClientProps) {
  return (
    <main className={`min-h-screen bg-gray-50 py-20 px-6 md:px-12 ${className}`}>
      <header className="mb-10">
        <h1 className="text-4xl font-bold text-[#FE904E]">{post.title}</h1>
      </header>

      <article className="prose max-w-none text-gray-700">
        <div dangerouslySetInnerHTML={{ __html: post.content }} />
      </article>
    </main>
  );
}
